.projectsSlides {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-color: #2d2d2d;
    height: 60%;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 100%;
    z-index: 10000;
    animation: slideup 2s backwards;
    box-shadow: 
    2px 2px 5px black;
}

.projectsLayer {
    position: absolute;
    top:0;
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: 1000;
    cursor: pointer;
}

.imgFrame {
    width: fit-content;
    overflow: hidden;
    cursor: pointer;
    position: relative;
    height: 100%;
    padding: 0;
    margin: 5px;
}


.projectsSlides img {
    width: 300px;
    height: 300px;
    transition: 1s transform;
    object-fit: cover;
    filter: brightness(0.8);
    transform: translateY(0%);
}

.projectsSlides .textover {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    display: none;
    text-align: center;
    color: #C9AD98;
    cursor: pointer;
    padding: 20px;
    font-size: 18px;
}

.imgFrame:hover .textover {
    display: block;
}

.imgFrame:hover .fa-external-link-alt {
    font-size: 40px;
    margin-top: 50px;
}

.imgFrame:hover img {
    transform: scale(1.5);
    filter: brightness(0.4);
}

.carousel {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    max-height: 300px;
    max-width: 1000px;
    margin: auto;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

.first {
    animation: zoomInnOut 1s 1.5s backwards;
}

.second {
    animation: zoomInnOut 1s 2s backwards;
}

.third {
    animation: zoomInnOut 1s 2.5s backwards;
}

.fa-caret-left, .fa-caret-right {
    font-size: 60px;
    cursor: pointer;
    color: #A7BBD3;
    text-shadow: 
      -1px -1px 1px #111, 
      2px 2px 1px #363636;
      z-index: 10000;
}
.fa-caret-left{
    float: left;
    position: absolute;
    left: 10px;
    top: 50%;
    transform: translateY(-50%);
}
.fa-caret-right {
    float: right;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
}
h2 {
    flex: 0 0 100%;
    font-family: 'Titillium Web', sans-serif;
    word-spacing: 7px;
    text-transform: uppercase;
    width: fit-content;
    color: #A7BBD3;
    letter-spacing: .1em;
    text-shadow: 
      -1px -1px 1px #111, 
      2px 2px 1px #363636;
    padding: 2px;
    display: block;
    margin: auto;

}

p {
    
    font-family: 'Titillium Web', sans-serif;
}

@keyframes slideup {
    from {top: 400px; opacity: 0} 
    to {top: 50%; opacity: 1}
}


@keyframes zoomInnOut {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.05);
    }
    100% {
        transform: scale(1);
    }
}

@keyframes bigger {
    from {transform: scale(0);} 
    to {transform: scale(1);}
}

@media only screen and (max-width:1050px) {
    .third {
        display: none;
    }
}
@media only screen and (max-width:750px) {
    .first {
        display: none;
    }
}