body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hero {
    position: absolute;
    top: 60%;
    left: 50%;
    display: block;
    margin-left: -160px;
}

@-webkit-keyframes moveInBottom {
    0% {
      opacity: 0;
      -webkit-transform: translateY(30px);
              transform: translateY(30px); 
    }
    100% {
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
      opacity: 1; 
    } 
}

@keyframes moveInBottom {
    0% {
      opacity: 0;
      -webkit-transform: translateY(30px);
              transform: translateY(30px); 
    }
    100% {
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
      opacity: 1; 
    } 
}
  
.btn:link, .btn:visited {
    text-transform: uppercase;
    text-decoration: none;
    padding: 15px 0px;
    width: 260px;
    text-align: center;
    display: inline-block;
    border-radius: 100px;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    position: relative; 
}
  
.btn:hover {
    -webkit-transform: translateY(-6px);
            transform: translateY(-6px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.btn:hover::after {
    -webkit-transform: scaleX(1.5) scaleY(1.5);
            transform: scaleX(1.5) scaleY(1.5);
    opacity: 0; 
}
  
.btn:active {
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px);
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}
  
.btn-white {
    background-color: white;
    color: #777; 
}
    
.btn-white::after {
    background-color: white; 
}
  
.btn::after {
    content: "";
    display: inline-block;
    height: 100%;
    width: 100%;
    border-radius: 100px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    -webkit-transition: all 0.4s;
    transition: all 0.4s; 
}
  
.btn-animated {
    -webkit-animation: moveInBottom 0.5s ease-in 0.75s;
            animation: moveInBottom 0.5s ease-in 0.75s;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards; 
}

@media only screen and (max-width:1000px) {
    .hero {
        margin: 0;
        padding: 0;
        right: 1%;
    }
}
.sidebar {
    background-color: rgba(245,245,220,0.7);
    position: relative;
    top: 0;
    left: 0;
    height: 100vh;
    width: 330px;
    text-align: center;
    -webkit-animation: slideIn 3s;
            animation: slideIn 3s;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards; 
    font-family: 'Titillium Web', sans-serif;
    overflow: hidden;
    padding: 0 5px;
}

.me {
    height: 45%;
    display: inline-block;
    box-shadow:0px 0px 50px 30px #f5f5dc inset;
    border-radius: 0 0 10px 10px;
    -webkit-filter: brightness(0.7);
            filter: brightness(0.7);
    max-width: 100%;
    object-fit: cover;
    margin-bottom: 10%;
}

.quote {
    display: block;
    margin: auto;
    max-width: 70%;
}

.quotes {
    font-size: 200%;
    color: #C9AD98;
    text-shadow: 1px 1px 1px #8c796a;
}

.fa-quote-left {
    float: left;
}

.fa-quote-right {
    float: right;
}

.socials {
    font-size: 200%;
    position: relative;
    margin-top: 25%;
}

.socials i {
    color: #C9AD98;
    margin: 10px;
    text-shadow: 1px 1px 1px #8c796a;
}
.socials i:hover {
    -webkit-animation: scaleUp 1s;
            animation: scaleUp 1s;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards; 
}

@-webkit-keyframes slideIn {
    0% {
        left: -100%;
        opacity: 0;
    }
    100% {
        left: 0;
        opacity: 1;
    } 
}

@keyframes slideIn {
    0% {
        left: -100%;
        opacity: 0;
    }
    100% {
        left: 0;
        opacity: 1;
    } 
}

@-webkit-keyframes scaleUp {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes scaleUp {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
.aboutMe {
    background-color: rgba(245,245,220,0.7);
    position: absolute;
    top: 40px;
    left: 340px;
    border-radius: 0 50px 50px 0;
    width: 100px;
    height: 60px;
    cursor: pointer;
    overflow: hidden;
    display: none;
}

.info {
    visibility: hidden;
}

.arrow_box {
	position: absolute;
	background: rgba(245,245,220,0.7);
	border: 1px solid rgba(245,245,220,0.7);
    top: 111px;
    left: 345px;
    border-radius: 10px;
    width: 280px;
}
.arrow_box:after, .arrow_box:before {
	bottom: 100%;
	left: 50%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
}

.arrow_box:after {
	border-color: rgba(245, 245, 220, 0);
	border-bottom-color: rgba(245,245,220,0.7);
	border-width: 10px;
	margin-left: -10px;
}
.arrow_box:before {
	border-color: rgba(245, 245, 220, 0);
	border-bottom-color: rgba(245,245,220,0.7);
	border-width: 11px;
	margin-left: -11px;
}
@-webkit-keyframes extend {
    from  {
        width: 100px;
    }
    to {
        width: 300px;
    }
}
@keyframes extend {
    from  {
        width: 100px;
    }
    to {
        width: 300px;
    }
}
@-webkit-keyframes shrink {
    from  {
        width: 300px;
    }
    to {
        width: 100px;
    }
}
@keyframes shrink {
    from  {
        width: 300px;
    }
    to {
        width: 100px;
    }
}
@-webkit-keyframes fadeIn {
    from  {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes fadeIn {
    from  {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@-webkit-keyframes fadeOut {
    from  {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}
@keyframes fadeOut {
    from  {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}
.projectsSlides {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    background-color: #2d2d2d;
    height: 60%;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 100%;
    z-index: 10000;
    -webkit-animation: slideup 2s backwards;
            animation: slideup 2s backwards;
    box-shadow: 
    2px 2px 5px black;
}

.projectsLayer {
    position: absolute;
    top:0;
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: 1000;
    cursor: pointer;
}

.imgFrame {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    overflow: hidden;
    cursor: pointer;
    position: relative;
    height: 100%;
    padding: 0;
    margin: 5px;
}


.projectsSlides img {
    width: 300px;
    height: 300px;
    -webkit-transition: 1s -webkit-transform;
    transition: 1s -webkit-transform;
    transition: 1s transform;
    transition: 1s transform, 1s -webkit-transform;
    object-fit: cover;
    -webkit-filter: brightness(0.8);
            filter: brightness(0.8);
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
}

.projectsSlides .textover {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    cursor: pointer;
    display: none;
    text-align: center;
    color: #C9AD98;
    cursor: pointer;
    padding: 20px;
    font-size: 18px;
}

.imgFrame:hover .textover {
    display: block;
}

.imgFrame:hover .fa-external-link-alt {
    font-size: 40px;
    margin-top: 50px;
}

.imgFrame:hover img {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    -webkit-filter: brightness(0.4);
            filter: brightness(0.4);
}

.carousel {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    max-height: 300px;
    max-width: 1000px;
    margin: auto;
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.first {
    -webkit-animation: zoomInnOut 1s 1.5s backwards;
            animation: zoomInnOut 1s 1.5s backwards;
}

.second {
    -webkit-animation: zoomInnOut 1s 2s backwards;
            animation: zoomInnOut 1s 2s backwards;
}

.third {
    -webkit-animation: zoomInnOut 1s 2.5s backwards;
            animation: zoomInnOut 1s 2.5s backwards;
}

.fa-caret-left, .fa-caret-right {
    font-size: 60px;
    cursor: pointer;
    color: #A7BBD3;
    text-shadow: 
      -1px -1px 1px #111, 
      2px 2px 1px #363636;
      z-index: 10000;
}
.fa-caret-left{
    float: left;
    position: absolute;
    left: 10px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.fa-caret-right {
    float: right;
    position: absolute;
    right: 10px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
h2 {
    flex: 0 0 100%;
    font-family: 'Titillium Web', sans-serif;
    word-spacing: 7px;
    text-transform: uppercase;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    color: #A7BBD3;
    letter-spacing: .1em;
    text-shadow: 
      -1px -1px 1px #111, 
      2px 2px 1px #363636;
    padding: 2px;
    display: block;
    margin: auto;

}

p {
    
    font-family: 'Titillium Web', sans-serif;
}

@-webkit-keyframes slideup {
    from {top: 400px; opacity: 0} 
    to {top: 50%; opacity: 1}
}

@keyframes slideup {
    from {top: 400px; opacity: 0} 
    to {top: 50%; opacity: 1}
}


@-webkit-keyframes zoomInnOut {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50% {
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}


@keyframes zoomInnOut {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50% {
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@-webkit-keyframes bigger {
    from {-webkit-transform: scale(0);transform: scale(0);} 
    to {-webkit-transform: scale(1);transform: scale(1);}
}

@keyframes bigger {
    from {-webkit-transform: scale(0);transform: scale(0);} 
    to {-webkit-transform: scale(1);transform: scale(1);}
}

@media only screen and (max-width:1050px) {
    .third {
        display: none;
    }
}
@media only screen and (max-width:750px) {
    .first {
        display: none;
    }
}
