.aboutMe {
    background-color: rgba(245,245,220,0.7);
    position: absolute;
    top: 40px;
    left: 340px;
    border-radius: 0 50px 50px 0;
    width: 100px;
    height: 60px;
    cursor: pointer;
    overflow: hidden;
    display: none;
}

.info {
    visibility: hidden;
}

.arrow_box {
	position: absolute;
	background: rgba(245,245,220,0.7);
	border: 1px solid rgba(245,245,220,0.7);
    top: 111px;
    left: 345px;
    border-radius: 10px;
    width: 280px;
}
.arrow_box:after, .arrow_box:before {
	bottom: 100%;
	left: 50%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
}

.arrow_box:after {
	border-color: rgba(245, 245, 220, 0);
	border-bottom-color: rgba(245,245,220,0.7);
	border-width: 10px;
	margin-left: -10px;
}
.arrow_box:before {
	border-color: rgba(245, 245, 220, 0);
	border-bottom-color: rgba(245,245,220,0.7);
	border-width: 11px;
	margin-left: -11px;
}
@keyframes extend {
    from  {
        width: 100px;
    }
    to {
        width: 300px;
    }
}
@keyframes shrink {
    from  {
        width: 300px;
    }
    to {
        width: 100px;
    }
}
@keyframes fadeIn {
    from  {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes fadeOut {
    from  {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}