.sidebar {
    background-color: rgba(245,245,220,0.7);
    position: relative;
    top: 0;
    left: 0;
    height: 100vh;
    width: 330px;
    text-align: center;
    animation: slideIn 3s;
    animation-fill-mode: backwards; 
    font-family: 'Titillium Web', sans-serif;
    overflow: hidden;
    padding: 0 5px;
}

.me {
    height: 45%;
    display: inline-block;
    box-shadow:0px 0px 50px 30px #f5f5dc inset;
    border-radius: 0 0 10px 10px;
    filter: brightness(0.7);
    max-width: 100%;
    object-fit: cover;
    margin-bottom: 10%;
}

.quote {
    display: block;
    margin: auto;
    max-width: 70%;
}

.quotes {
    font-size: 200%;
    color: #C9AD98;
    text-shadow: 1px 1px 1px #8c796a;
}

.fa-quote-left {
    float: left;
}

.fa-quote-right {
    float: right;
}

.socials {
    font-size: 200%;
    position: relative;
    margin-top: 25%;
}

.socials i {
    color: #C9AD98;
    margin: 10px;
    text-shadow: 1px 1px 1px #8c796a;
}
.socials i:hover {
    animation: scaleUp 1s;
    animation-fill-mode: backwards; 
}

@keyframes slideIn {
    0% {
        left: -100%;
        opacity: 0;
    }
    100% {
        left: 0;
        opacity: 1;
    } 
}

@keyframes scaleUp {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.2);
    }
    100% {
        transform: scale(1);
    }
}